header { 
  top: 0;
  width: auto;
  margin: 0 auto;
  position: relative;
  font-size: 12vw;
}

h1 {
  text-align: center;
  font: 12vw Handlee;
  color: rgb(var(--pen-color));
  position: relative;
  opacity: 0;
  -webkit-animation: Splash_appear__XAyGe 0.5s ease-in-out forwards 2s;
          animation: Splash_appear__XAyGe 0.5s ease-in-out forwards 2s;
}

h1 > span {
  position: relative;
}

h1 > span:not(:last-child) {
  margin-right: 0.2ch
}

h1 > span > span {
  opacity: 0;
  display: inline-block;
  position: absolute;
  top: 0;
  left: 0.5ch;
  font-family: pen;
} 

h1 > span:nth-child(2) > span, h1 span:nth-child(6) > span{
  -webkit-animation: Splash_show-splash-letter__IWTIa var(--guess_gap) ease forwards calc(var(--guess_gap) * 2 + 2s);
          animation: Splash_show-splash-letter__IWTIa var(--guess_gap) ease forwards calc(var(--guess_gap) * 2 + 2s);
}

h1 > span:nth-child(5) > span {
  -webkit-animation: Splash_show-splash-letter__IWTIa var(--guess_gap) ease forwards calc(var(--guess_gap) * 3 + 2s);
          animation: Splash_show-splash-letter__IWTIa var(--guess_gap) ease forwards calc(var(--guess_gap) * 3 + 2s);
}

h1 > span:nth-child(3) > span, h1 span:nth-child(7) > span {
  -webkit-animation: Splash_show-splash-letter__IWTIa var(--guess_gap) ease forwards calc(var(--guess_gap) * 4 + 2s);
          animation: Splash_show-splash-letter__IWTIa var(--guess_gap) ease forwards calc(var(--guess_gap) * 4 + 2s);
}

h1 > span:nth-child(1) > span {
  -webkit-animation: Splash_show-splash-letter__IWTIa var(--guess_gap) ease forwards calc(var(--guess_gap) * 5 + 2s);
          animation: Splash_show-splash-letter__IWTIa var(--guess_gap) ease forwards calc(var(--guess_gap) * 5 + 2s);
}

h1 > span:nth-child(4) > span {
  -webkit-animation: Splash_show-splash-letter__IWTIa var(--guess_gap) ease forwards calc(var(--guess_gap) * 6 + 2s);
          animation: Splash_show-splash-letter__IWTIa var(--guess_gap) ease forwards calc(var(--guess_gap) * 6 + 2s);
}

 @-webkit-keyframes Splash_show-splash-letter__IWTIa {
  to { 
    opacity: 1;  
  }
}

 @keyframes Splash_show-splash-letter__IWTIa {
  to { 
    opacity: 1;  
  }
} 

@-webkit-keyframes Splash_appear__XAyGe {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
} 

@keyframes Splash_appear__XAyGe {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
}


@font-face {
  font-family: pen;
  src: url(/Hangman/static/media/PencilSharp.b4301baf.ttf);
}

:root {
  --guess_gap: 0.5s;
  --pen-color: 23, 0, 85;
}

:root, #root {
  height: 100%;
}

body {
  position: absolute;
  background: url(/Hangman/static/media/plank-wood-texture-diffuse.3a697931.jpeg), burlywood;
  background-position: center center;
  padding: 0;
  margin: 0;
  /* overflow: hidden; */
  width: 100%;
  height: 100%;
  font-family: pen;
}

.App {
  display: flex;
  width: 90%;
  min-height: 100%;
  margin: auto;
  align-items: center;
  padding: 0;
  line-height: 3em;
  background: 
    linear-gradient(to right, transparent 0, transparent 8em, #dc143c28 8em, #dc143c28 8.0625em, transparent 8.0625em),
    linear-gradient(#fffffc 0, #fffffc 6em, transparent 6em, transparent),
    repeating-linear-gradient(to bottom, transparent 0, transparent 3em, #00008036 3em, #00008036 3.0625em, transparent 3.0625em), 
    linear-gradient(140deg, #fffffc 60%, #f0f0db 90%, #e0e0d1 100%)
    #fffffc;
  background-position: 0 0,0 0, 0 0;
  box-shadow: rgba(0, 0, 0, 0.535) 2px 2px 30px;
  position: relative;
}

.App:focus {
    outline: none;
}

#Splash {
    opacity: 1;
    transition: opacity 1s ease-out;
}

#Splash h1:hover{
    color: black;
}

#Splash a {
    text-decoration: none;
}

@-webkit-keyframes fade-out {
    0% {
        opacity: 1
    }
    100% {
      opacity: 0;
      display: none;
    }
  }

@keyframes fade-out {
    0% {
        opacity: 1
    }
    100% {
      opacity: 0;
      display: none;
    }
  }

#Splash.fade-out {
    opacity: 0;
}


.App.spin-out {
  animation: transition 2s ease, transition 2s ease 0s 1 reverse forwards;
  -webkit-animation-play-state: paused, running;
          animation-play-state: paused, running;
}

.App.spin-in {
  -webkit-animation: transition 2s ease;
          animation: transition 2s ease;
  -webkit-animation-play-state: running;
          animation-play-state: running;
  -webkit-animation-play-state: initial;
          animation-play-state: initial;
}

@-webkit-keyframes transition {
  from {
    opacity: 0;
    -webkit-transform: 
      scale(200%)
      translate(0, -100%)
      rotateZ(90deg);
            transform: 
      scale(200%)
      translate(0, -100%)
      rotateZ(90deg)
  }
  to {
    opacity: 1;
    -webkit-transform: 
      scale(100%)
      translate(0, 0)
      rotateZ(0);
            transform: 
      scale(100%)
      translate(0, 0)
      rotateZ(0)
  }
}

@keyframes transition {
  from {
    opacity: 0;
    -webkit-transform: 
      scale(200%)
      translate(0, -100%)
      rotateZ(90deg);
            transform: 
      scale(200%)
      translate(0, -100%)
      rotateZ(90deg)
  }
  to {
    opacity: 1;
    -webkit-transform: 
      scale(100%)
      translate(0, 0)
      rotateZ(0);
            transform: 
      scale(100%)
      translate(0, 0)
      rotateZ(0)
  }
}

textarea {
  display: none;
}

@media (max-width: 650px) {

  .App {
    width: 100%;
  }

  .App > * {
    flex-flow: column;
  }

  svg {
    height: 50vh;
  }
  
}
